body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100 vh;
  width: 100vw;
  text-align: center;
  background: #2193b0; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#6dd5ed),
    to(#2193b0)
  );
  background: linear-gradient(
    to right,
    #6dd5ed,
    #2193b0
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  overflow: hidden;
  margin: 5px;
}

.node {
  width: 1.5vw;
  height: 3vh;
  margin: 0px auto;
  border: 1px solid purple;
}
.node-Visited {
  width: 1.5vw;
  height: 3vh;
  margin: 0px;
  border: 1px solid purple;
  -webkit-animation-name: node-visit;
          animation-name: node-visit;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  background: yellowgreen;
}
@-webkit-keyframes node-visit {
  0% {
    background-color: yellow;
  }
  100% {
    background-color: yellowgreen;
  }
}
@keyframes node-visit {
  0% {
    background-color: yellow;
  }
  100% {
    background-color: yellowgreen;
  }
}
@-webkit-keyframes shortest-path {
  0% {
    background-color: purple;
  }
  100% {
    background-color: white;
  }
}
@keyframes shortest-path {
  0% {
    background-color: purple;
  }
  100% {
    background-color: white;
  }
}
.node-Path {
  width: 1.5vw;
  height: 3vh;
  margin: 0px;
  border: 1px solid purple;
  -webkit-animation-name: shortest-path;
          animation-name: shortest-path;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  background-color: white;
}

.nodestart {
  width: 1.5vw;
  height: 3vh;
  margin: 0px;
  border: 1px solid purple;
  background: green;
}
.nodestart:before {
  content: 'S';
}
.nodefinish {
  width: 1.5vw;
  height: 3vh;
  margin: 0px;
  border: 1px solid purple;
  background: red;
}
.nodefinish:before {
  content: 'E';
}
.nodewall {
  width: 1.5vw;
  height: 3vh;
  margin: 0px;
  border: 1px solid purple;
  background: purple;
}

.grid {
  display: flex;
  flex-direction: start;
  justify-content: center;
  align-content: center;
  margin-top: 50px;
}
button {
  border-radius: 5px;
  height: 40px;
  width: 90px;
  margin: 5px 20px;
}
@media (max-width: 1200px) {
  button {
    font-size: 7px;
  }
}

.navBar {
  font-family: 'Fira Code';
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10vh;
  color: #000;
  width: 100vw;
}
.navBar .split {
  display: flex;
  flex-direction: row;
}
.navBar span {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  margin: 0 5px;
}
.endNode {
  width: 1.5vw;
  height: 3vh;
  margin: 5px;
  border: 1px purple solid;
  background: red;
}
.endNode:before {
  content: 'E';
  vertical-align: -20%;
}
.startNode {
  width: 1.5vw;
  height: 3vh;
  margin: 5px;
  border: 1px purple solid;
  background: green;
}
.startNode:before {
  content: 'S';
  vertical-align: -20%;
}
.visitedNode {
  width: 1.5vw;
  height: 3vh;
  margin: 5px;
  border: 1px purple solid;
  background: yellowgreen;
}
.unvisitedNode {
  width: 1.5vw;
  height: 3vh;
  margin: 5px;
  border: 1px purple solid;
  background: -webkit-gradient(linear, left top, right top, from(#6dd5ed), to(#2193b0));
  background: linear-gradient(to right, #6dd5ed, #2193b0);
}
.path {
  width: 1.5vw;
  height: 3vh;
  margin: 5px;
  border: 1px purple solid;
  background: white;
}
.wall {
  width: 1.5vw;
  height: 3vh;
  margin: 5px;
  border: 1px purple solid;
  background: purple;
}
@media (max-width: 1200px) {
  .navBar span {
    font-size: 7px;
  }
  .navBar {
    flex-direction: column;
  }
  .navBar select {
    font-size: 7px;
  }
}

